body, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, li {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.btn {
  width: 300px;
  height: 50px;
  cursor: pointer;
}

:root {
  --modal-bg: #fff;
  --backdrop-bg: rgba(0, 0, 0, .2);
  --fontFamily-roboto: "Roboto", normal;
  --font-black: #000;
  --font-gray: #8c8c8c;
  --font-btn-watched: #ff6b01;
  --font-btn-not-selected: #f7f7f7;
  --cubic-bezier: cubic-bezier(.4, 0, .2, 1);
}

.backdrop {
  height: 100%;
  width: 100%;
  background-color: var(--backdrop-bg);
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  width: 280px;
  height: 90%;
  background-color: var(--modal-bg);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 704px;
    height: auto;
  }
}

@media screen and (min-width: 1024px) {
  .modal {
    width: 806px;
    height: auto;
  }
}

.modal__btn-close {
  width: 30px;
  height: 30px;
  color: var(--font-black);
  cursor: pointer;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-container {
  padding: 48px 20px 40px;
}

@media screen and (min-width: 768px) {
  .modal-container {
    padding: 40px 36px;
    display: flex;
  }
}

@media screen and (min-width: 1024px) {
