  }
}

.modal__btn.watched {
  background-color: var(--font-btn-watched);
  text-transform: uppercase;
  font-family: var(--fontFamily-roboto);
  color: var(--modal-bg);
  border: none;
  margin-right: 15px;
}

.modal__btn.watched:focus, .modal__btn.watched:hover {
  box-shadow: 0 0 10px 0 var(--font-btn-watched) inset, 0 0 10px 4px var(--font-gray);
  border-color: #ff6b01;
}

.modal__btn.queue {
  border: 1px solid var(--font-black);
